
.forsearch{
	width:100%;
	height:470px;
	margin-top:50px;
	box-shadow:0px 0px 1px 0px rgba(0,0,0,0.19);
	background-image:url("assets/data.png");
	background-size:cover;
}
.searcholder{
	width:50%;
	padding:10px;
	align-items:center;
	display:flex;
    flex-direction:column;
	margin:0px auto;
}
.fortitle{
	font-size:25px;
	font-family:"Times New Romans"
}
.searchinput{
	display:flex;
	flex-direction:row;
	width:70%;
	height:auto;
	margin-left:20px;
	align-items:center;
}
.forinputtype{
	width:60%;

}
.searchinput input{
	padding:10px;
	box-shadow:0px 0px 10px 0px rgba(0,0,0,0.19);
	border:1px solid white;
	border-radius:5px 0px 0px 5px;
	width:100%;
	margin-left:30px;
}
.searchinput input:focus{
	outline:none;
}
.searchbutton{
	margin-left:4px;
	width:10%;
}
.searchinput button{
	padding:10px;
	box-shadow:0px 0px 2px 0px rgba(0,0,0,0.19);
	border:1px solid white;
	border-radius:0px 5px 5px 0px;
	background:white;
}
.searchinput button:hover{
	cursor:pointer;
}
.searchinput button:focus{
	outline:none;
}