.restaurantlist{
	width:100%;
	min-height:600px;
	height:auto;
	background:#f5fafc;
}
.serestaurantlist{
	width:100%;
	min-height:1200px;
	height:auto;
	background:#f5fafc;
}
.restaurantlist h1{
	font-size:35px;
	font-family:"Times New Romans";
	margin-top:3rem;
}
.forcards{
	width:100%;
	margin-top:20px;
	min-height:280px;
	height:auto;
	box-shadow:0px 0px 1px 0px rgba(0,0,0,0.19);
	padding:10px;
	display:flex;
	flex-direction:row;
	flex-wrap:wrap;
	justify-content:space-between;
	box-sizing:border-box;
}
.card{
	width:245px;
	height:270px;
	box-shadow:0px 0px 10px 0px rgba(0,0,0,0.19);
	background:white;
	border-radius:5px;
	margin-left:20px;
	margin-top:20px;
	display:flex;
	flex-direction:column;
}
.card:hover{
cursor:pointer;
}
.Link{
	text-decoration:none;
	color:black;
}
.img{
	width:100%;
	height:200px;
	background:white;
	border-radius:5px 5px 0px 0px
}
.img img{
	width:100%;
	height:198px;
}
.info{
	width:100%;
	height:69px;
	background:white;
	display:flex;
	flex-direction:row;
	justify-content:space-between;
	align-items:center;
}

.forthumb{
	width:70px;
	height:68px;
}
.forthumb img{
	margin-left:5px;
	margin-top:10px;
	float:left;
}
.fordesc{
	width:180px;
	height:68px;
	display:flex;
	flex-direction:column;
	justify-content:space-around;
	align-items:left;
}
.fordesc h2{
	margin-left:-5px;
	float:left;
	color:black;
	font-size:16px;
}
.fordesc p{
	margin-top:-5px;
	margin-left:-45px;
	color:black;
	font-size:14px;
	opacity:0.7;
}
.exploremore{
	width:100%;
	height:60px;
	margin-top:40px;
	background:transparent;

}
.exbutton{
	padding:10px;
	width:150px;
	margin-top:30px;
	border-radius:5px;
	border:1px solid #ff6000;
	background:#ff6000;
	color:white;
}