.getapp{
	width:100%;
	height:300px;
	background:#ff6000;
	display:flex;
	flex-direction:column;
	flex-wrap:wrap;
}
.phone{
	width:50%;
	height:280px;
	box-sizing:border-box;
}
.phone img{
	height:480px;
	margin-top:-80px;
}
.Getapp{
	width:40%;
	height:280px;
	margin-left:-80px;
	color:white;
}
.Getapp button{
	padding:10px;
	width:150px;
	border-radius:5px;
	border:1px solid white;
	color:#ff6000;
	cursor:pointer;
}
.Getapp button:hover{
	cursor:pointer;
}
