.HowitWorks{
	width:100%;
	height:500px;
	background:white;
}
.HowitWorks h1{
	font-size:35px;
	font-family:"Times New Romans";
	margin-top:5rem;
}
.steps{
	width:100%;
	height:300px;
	background:white;
	box-shadow:0px 0px 0px 0px rgba(0,0,0,0.19);
	padding:10px;
	display:flex;
	flex-direction:row;
	flex-wrap:wrap;
	justify-content:space-around;
	box-sizing:border-box;

}
.steps div{
	box-shadow:0px 0px 0px 0px rgba(0,0,0,0.19);
	width:30%;
	display:flex;
	flex-direction:column;
	justify-content:space-around;
}
.steps div img{
	width:80px;

}