.restodetail{
width:100%;
min-height:600px;
height:auto;
margin-top:9px;
background:red;
}
.restoheader{
	width:100%;
	height:350px;
	margin-top:30px;
	box-sizing: border-box;
	background-size: cover;
	background-image:url("./assets/resto_bg.png");
}
.searchheader{
	width:100%;
	height:350px;
	margin-top:30px;
	box-sizing: border-box;
	background-size: cover;
	background:white;
	box-shadow:0px 0px 2px 0px rgba(0,0,0,0.19);
}
.forheaderthumb{
	width:50%;
	height:350px;
	height:100px;
	display:flex;
	flex-direction:row;
	align-items:center;
}
.forheaderthumb section{
margin-left:60px;
color:white;
}
.forheaderthumb section img{
	margin-left:60px;
}
.forheaderthumb section h3,h5{
	margin-left:-40px;
}
.forheaderthumb section h5{
	margin-left:-140px;
}
.restocontent{
	width:100%;
	min-height:650px;
	height:auto;
	margin-top:-16px;
	box-sizing:border-box;
	background:#f5fafc;
	display:flex;
	flex-direction:row;
	flex-wrap: wrap;
	justify-content:space-around;
}
.restocontent .forrateandinfo,.formenu{
background:white;
margin-top:50px;
box-sizing: border-box;
box-shadow:0px 0px 10px 0px rgba(0,0,0,0.19);
}
.formenu{
width:40%;
height:400px;
margin-left:-40px;
}
.forrateandinfo{
   width:30%;
height:580px;
display:flex;
flex-direction:column;
}
.forlink{
	width:100%;
	height:50px;
	box-shadow:0px 0px 3px 0px  rgba(0,0,0,0.19);
}
.forlink ul{
	display:flex;
	flex-direction:row;
	justify-content:flex-start;
	list-style:none;
	line-height:20px;
}
.forlink ul li{
	margin-left:20px;
	height:50px;

}
.forlink ul li:hover{
	color:#ff6000;
}
.Link{
	text-decoration:none;
	color:black;
}
.foractive{
	color:#ff6000;
	border-bottom:5px solid #ff6000;
}
.forInfo{
	display:flex;
	flex-direction:column;
	justify-content:space-around;
}
.forInfo section{
	display:flex;
	flex-direction:row;
	justify-content:space-around;
	align-items:center;
}
.forInfo section label{
	text-align:left;
}
.forInfo section p{
	opacity:0.7;
	float:left;
}
.linkscontents{
	width:100%;
	height:auto;
	box-shadow:0px 0px 1px 0px rgba(0,0,0,0.19);
	background:white;
	margin-top:10px;
}

.forRating{
height:auto;
min-height:400px;
}
.forRating ul{
	display:flex;
	flex-direction:column;
	justify-content:space-around;
}
.forRating ul li{
list-style-type:circle;
list-style:none;
width:30px;
height:30px;
margin-left:10px;
margin-top:60px;
background:black;
border-radius:50px;
border:1px solid black;
}
.forRating ul li h3,h5{
	margin-left:50px;
	margin-top:-2px;
}
.forRating ul li h3{
	font-size:22px;
	font-family:"Times New Roman"
}
.forRating ul li h5{
	color:#ff6000;
	font-size:12px;
	width:100px;
	margin-left:19px;
	margin-top:-18px;
}
.forRating ul li p{
	width:300px;
	padding:2px;
	margin-top:;
}
.formenutitle{
	color:#ff6000;
width:100px;
height:30px;
padding:5px;
margin-left:20px;
margin-top:-2px;
}
.formenutitle p{
	font-size:22px;
	font-family:"Times New Romans";
	font-weight:105px;
}