.contact{
	width:100%;
	height:400px;
	background:#3b3b3b;
	margin-top:-20px;
	display:grid;
	grid-template-columns:48% 49%;
	grid-template-rows:auto; 
	grid-column-gap:10px;
	box-shadow:0px 0px 10px 0px rgba(0,0,0,0.19);
}
.contact .contactinfo{
	border-right:1px solid black;
	display:flex;
	flex-direction:column;
	justify-content:space-around;
	align-items:center;
	color:white;
	opacity:0.8;
}
.contactinfo h2{
	color:white;
	text-align:center;
	font-size:25px;
	margin-top:40px;
	font-family:"Times New Romans"
}
.email{
	margin-left:55px;
}
.addre{
	margin-left:-15px;
}
.form{
	width:100%;
	padding:10px;
	height:250px;
	margin:40px auto;
	box-sizing:border-box;
	background:transparent;
}
input,textarea{
	padding:10px;
	border:1px solid white;
	border-radius:5px;
	
}
form input{
	margin-left:10px;
	width:180px;
}
form textarea{
	margin-left:15px;
}
.buttoni{
	padding:10px;
	width:150px;
	border-radius:5px;
	border:1px solid #ff6000;
	float:right;
	margin-right:100px;
	margin-top:20px;
	background:#ff6000;
	color:white;
	cursor:pointer;
}
.buttoni:hover{
	cursor:pointer;
}